import JsonP from 'jsonp';
import axios from 'axios';

export default class Axios{
	  static jsonp(options) {
	    return new Promise((resolve, reject) => {
	        JsonP(options.url, {
	            param: 'callback'
	        }, function (err, response) {
	            if (response.status == 'success') {
	                resolve(response);
	            } else {
	                reject(response.messsage);
	            }
	        })
	    })
	}

	static IPXY(options) {
		return new Promise((resolve, reject) => {
				JsonP(options.url, {
						param: 'callback'
				}, function (err, response) {
						if (response.status == '0') {
								resolve(response);
						} else {
								reject(response);
						}
				})
		})
}



		static requestList(_this,url,params,results,isMock){
			// 4个数据  this 代表当前对象 ,url,params字段，分页记录条数,是否为Macl 测试api
				let data = {
					results,
					params
				}

				this.ajax({
					url,
					data,
					isMock
				}).then((res)=>{
					if(res){
						const pagination = { ..._this.state.pagination };
						 pagination.total = 200;
						 const result = res.data.result.item_list;
						 _this.setState({
							 loading: false,
							 list:result,
							 pagination,
						 })
					}
				})
		}




	 static ajax(options) {
		 let loading;
		 if(options.data && options.data.isShowLoading !== false){
			loading= document.getElementById('ajaxLoading');
			loading.style.display='block';
		 }

		 let  baseApi= '';
		 if(options.isMock){
			  baseApi = 'https://www.easy-mock.com/mock/5cf1f2a93789183aee728de6';
		 }else{
			  baseApi = 'https://www.easy-mock.com/mock/5cf1f2a93789183aee728de6';
		 }

	    return new Promise((resolve, reject) => {
	       axios({
			   url:options.url,
			   method:'get',
			   baseURL:baseApi,
			   timeout:5000,
			   params:(options.data && options.data.params) || ''
		   }).then((response)=>{

			   if(options.data && options.data.isShowLoading !== false){
			   			loading = document.getElementById('ajaxLoading');
			   			loading.style.display='none';
					}

			    const result = response.data;
			   if (response.status == '200' && (result.data.code == 0 || result.success)) {
			       resolve(result);
			   } else {
			       reject(response.messsage);
			   }

		   })
	    })
	}

}
