import styled from 'styled-components';

export const TabWrap = styled.div`
	height:calc(100vh);
	.meunWrap{
		border:none;
		margin-top:-3px;
}


`;

export const Applogo = styled.div`
	width:inhirt;
	height:calc(15vh);
	line-height:99px;
	position: relative;

	overflow:hidden;
	p{
		line-height:35px;
		position: absolute;
		left:10px;
		top:30px;
	}

	img{
		height:35px;
		margin:5px;
	}
	h1{
		line-height:35px;
		display:inline;
		color:#fff;
		font-size:20px;
	}
	border-bottom:1px solid #fff;
`;
