import { combineReducers } from 'redux-immutable';
import { reducer as tabReducer }  from '../components/tab/store';
import { reducer as homeheaderReducer }  from '../components/homeheader/store';
// 拿过来header  里面的 store{reducer}   其实还是reducer


export default combineReducers({
	tab : tabReducer,
	homeheader:homeheaderReducer,
});
