import React,{Component } from 'react';
import {
	Wrap
} from './style';
import {Row,Col,Spin,Card ,Icon,Alert} from 'antd';

class SpinDemo extends Component {
	render(){
		const yuanquan = <Icon type="loading"></Icon>;
		return(
			<Wrap>
				<Card title="加载中...">
					<Spin />
				</Card>
				
				<Card title="大图标加载中...">
					<Spin size="large" />
				</Card>
				
				<Card title="自定义加载中...">
					<Spin indicator={yuanquan} size="large" tip="加载中..."/>
				</Card>
				
				<Card title="内容遮罩..." >
				
					<Spin indicator={yuanquan} size="large" tip="加载中...">
					<Alert 
						message="React"
						description="欢迎来到React高级实战课程"
						type="warning"
					/>
					</Spin>
					
				</Card>
			</Wrap>
		)
	}
}

export default SpinDemo;