	import React,{Component } from 'react';
	import axios from '../../axios';
	import Utils from '../../utils';
	import { Card, Button, Table, Form, Select,Modal, message } from 'antd';
	import {
		Wrap
	}from './style';
	const FormItem = Form.Item;
	const Option = Select.Option;


	export default class City extends Component{

	    state = {
	        list:[],
	        isShowOpenCity:false
	    }
	    params = {
	        page:1
	    }
	    componentDidMount(){
	        this.requestList();
	    }

	    // 默认请求我们的接口数据
	    requestList = ()=>{

				axios.requestList(this,'/adminapi/open_city',this.params,null,true);

	      // axios.ajax({
				// 	url:'/adminapi/open_city',
				// 	data:{
				// 		params:{
				// 			page:1
				// 		}
				// 	}
				// }).then((res)=>{
				// 		const result = res.data.result.item_list;
				// 		console.log(result);
				// 		this.setState({
				// 			list:result
				// 		})
				//
				// 	})
	    }

	    // 开通城市
	    handleOpenCity = ()=>{
	       this.setState({
			   isShowOpenCity:true
		   })
	    }

	    // 城市开通提交
	    handleSubmit = ()=>{
				let cityInfo = this.cityForm.props.form.getFieldsValue()
				console.log(cityInfo);



				axios.ajax({
					url:'/opencity',
					data:{
						params:cityInfo
					}
				}).then((res)=>{
					if(res.success){
						message.success(res.data);

						this.setState({
							isShowOpenCity:false
						})
						this.requestList();

					}else{
						message.error('开通失败');
					}
				})


	    }
	    render(){
	      const columns = [
			  {
				title: '城市ID',
				dataIndex: 'id',
				key: 'id'
			  },
			  {
				title: '城市名称',
				dataIndex: 'name',
				key: 'name'
			  },
			  {
				title: '用车模式',
				dataIndex: 'mode',
				key: 'mode',
				render(mode){
					return mode == 1? '停车点':'禁停区域'
				}
			  },
			 {
				title: '营运模式',
				dataIndex: 'op_mode',
				key: 'op_mode',
				render(mode){
					return mode == 1?'自营':'连锁店'
				}
			  },
			  {
				title: '授权加盟商',
				dataIndex: 'franchisee_name',
				key: 'franchisee_name'
			  },
			  {
				title: '城市管理员',
				dataIndex: 'city_admins',
				key: 'city_admins',
				render(arr){
					return arr.map((admins)=>{
						return admins.user_name
					}).join(',')
				}
			  },
			  {
				title: '城市开通时间',
				dataIndex: 'open_time',
				key: 'open_time'
			  },
			  {
				title: '操作时间',
				dataIndex: 'update_time',
				key: 'update_time',
				render:Utils.formDate
			  },
			  {
				title: '操作人',
				dataIndex: 'sys_user_name',
				key: 'sys_user_name'
			  }

		  ]
	        return (
	            <Wrap>

	                <Card>
	                    <FilterForm />
	                </Card>
	                <Card style={{ height:80}}>
	                    <Button type="primary" onClick={this.handleOpenCity}>开通城市</Button>
	                </Card>
	                <div className="content-wrap">
	                    <Table
	                        bordered
	                        columns={columns}
	                        dataSource={this.state.list}
	                        pagination={this.state.pagination}
	                    />
	                </div>
									<Modal
						          title="开通城市"
											visible={this.state.isShowOpenCity}
											okText="确认"
											onOk={this.handleSubmit}
											onCancel={()=>{
												this.setState({
													isShowOpenCity:false
												})
											}}
										>
										<OpenCityForm wrappedComponentRef={(formData)=>{
											this.cityForm = formData
										}}/>
									</Modal>

	            </Wrap>
	        );
	    }
	}

	class FilterForm extends React.Component{

	    render(){
	        const { getFieldDecorator } = this.props.form;
	        return (
	            <Form layout="inline">
	                <FormItem label="城市">
	                    {
	                        getFieldDecorator('city_id')(
	                            <Select
	                                style={{width:100}}
	                                placeholder="全部"
	                            >
	                                <Option value="">全部</Option>
	                                <Option value="1">北京市</Option>
	                                <Option value="2">天津市</Option>
	                                <Option value="3">深圳市</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="用车模式">
	                    {
	                        getFieldDecorator('mode')(
	                            <Select
	                                style={{ width: 120 }}
	                                placeholder="全部"
	                            >
	                                <Option value="">全部</Option>
	                                <Option value="1">指定停车点模式</Option>
	                                <Option value="2">禁停区模式</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="营运模式">
	                    {
	                        getFieldDecorator('op_mode')(
	                            <Select
	                                style={{ width: 80 }}
	                                placeholder="全部"
	                            >
	                                <Option value="">全部</Option>
	                                <Option value="1">自营</Option>
	                                <Option value="2">加盟</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="加盟商授权状态">
	                    {
	                        getFieldDecorator('auth_status')(
	                            <Select
	                                style={{ width: 100 }}
	                                placeholder="全部"
	                            >
	                                <Option value="">全部</Option>
	                                <Option value="1">已授权</Option>
	                                <Option value="2">未授权</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem>
	                    <Button type="primary" style={{margin:'0 20px'}}>查询</Button>
	                    <Button>重置</Button>
	                </FormItem>
	            </Form>
	        );
	    }
	}
	FilterForm = Form.create({})(FilterForm);

	class OpenCityForm extends React.Component{
	    render(){
	        const formItemLayout = {
	            labelCol:{
	                span:5
	            },
	            wrapperCol:{
	                span:19
	            }
	        }
	        const { getFieldDecorator }  =this.props.form;
	        return (
	            <Form layout="horizontal">
	                <FormItem label="选择城市" {...formItemLayout}>
	                    {
	                        getFieldDecorator('city_id',{
	                            initialValue:'1'
	                        })(
	                            <Select style={{ width: 100 }}>
	                                <Option value="">全部</Option>
	                                <Option value="1">北京市</Option>
	                                <Option value="2">天津市</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="营运模式" {...formItemLayout}>
	                    {
	                        getFieldDecorator('op_mode', {
	                            initialValue: '1'
	                        })(
	                            <Select style={{ width: 100 }}>
	                                <Option value="1">自营</Option>
	                                <Option value="2">加盟</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="用车模式" {...formItemLayout}>
	                    {
	                        getFieldDecorator('use_mode', {
	                            initialValue: '1'
	                        })(
	                            <Select style={{ width: 100 }}>
	                                <Option value="1">指定停车点</Option>
	                                <Option value="2">禁停区</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	            </Form>
	        );
	    }
	}
	OpenCityForm = Form.create({})(OpenCityForm);
