	import React,{Component } from 'react';
	import axios from '../../axios';
	import Utils from '../../utils';
	import ETable from '../../common/ETable';
	import data from '../../static/menuConfig';
	import { Card, Button, Table, Form, Select,Modal, message,Input,Radio,Tree } from 'antd';
	import {
		Wrap
	}from './style';

	const { TreeNode } = Tree;
	const FormItem = Form.Item;
	const Option = Select.Option;


	export default class Power extends Component{

	    state = {
	        list:[],
	        isShowCreate:false,
					isShowSetPrower:false,
					prowerInfo:"",
					menuInfo:""
	    }
	    params = {
				page:1
	    }
	    componentDidMount(){
	        this.requestList();
	    }

	    // 默认请求我们的接口数据
	    requestList = ()=>{
				axios.requestList(this,'/adminapi/role/list',this.params,null,true);
	    }

	    // 创建角色
	    handleCreateAuthor = ()=>{
	       this.setState({
			   isShowCreate:true
		   })
	    }

	    // 角色创建提交
	    handleSubmit = ()=>{
				let data = this.createForm.props.form.getFieldsValue();
				axios.ajax({
					url:'/role/create',
					data:{
						params:data
					}
				}).then((res)=>{
					if(res.data.code == 0){
						message.success('请求成功');

						this.setState({
							isShowCreate:false
						})
						this.requestList();

					}else{
						message.error('创建失败');
					}
				})


	    }
// ----------------------------------------------------------
			// 设置权限
			handleSetPower = ()=>{
					let item = this.state.selectedItem;
					if(!item){
						message.info('请选择一条数据进行操作 ');
						return;
					}else{
						this.setState({
							isShowSetPrower:true,
							prowerInfo:item

						})
					}

			}
			handleSetPowerSubmit = ()=>{
				let value = 	this.setPowerForm.props.form.getFieldsValue();
				// 选中的 id
				value.role_id = this.state.selectedItem.id;
				value.prowerInfo = this.state.prowerInfo;
				axios.ajax({
					url:'/adminapi/permission/edit',
					data:{
						params:{
							...value
						}
					}
				}).then((res)=>{
					if(res){
						this.setState({
							isShowSetPrower:false
						})
						axios.requestList(this,'/adminapi/role/list',this.params,null,true);
					}
				})
			}


	    render(){
	      const columns = [
			  {
				title: '角色ID',
				dataIndex: 'id',
			  },
			  {
				title: '角色名称',
				dataIndex: 'role_name',
			  },
				{
				 title: '创建时间',
				 dataIndex: 'create_time',
				 render:Utils.formDate
				 },
			  {
				title: '使用状态',
				dataIndex: 'status',
				render(mode){
					return mode == 1? '启用':'停用'
				}
			  },
			  {
				title: '授权时间',
				dataIndex: 'authorize_time',
				render:Utils.formDate
			  },
			  {
				title: '授权人',
				dataIndex: 'authorize_user_name',
			  }
		  ]
	        return (
	            <Wrap>
	                <Card style={{ height:80}}>
										<Button type="primary" onClick={this.handleCreateAuthor}>创建角色</Button>
										<Button type="primary" onClick={this.handleSetPower}>设置权限</Button>
										<Button type="primary" onClick={this.handleCreateAuthor}>创建角色</Button>
	                </Card>
	                <div className="content-wrap">
	                    <ETable
													updateSelectedItem ={Utils.updateSelectedItem.bind(this)}
													selectedRows = {this.state.selectedRows}
													selectedRowKeys = {this.state.selectedRowKeys}
	                        bordered
	                        columns={columns}
	                        dataSource={this.state.list}
	                        pagination={this.state.pagination}
	                    />
	                </div>

									<Modal
						          title="创建角色"
											visible={this.state.isShowCreate}
											okText="确认"
											onOk={this.handleSubmit}
											onCancel={()=>{
												this.setState({
													isShowCreate:false
												})
											}}
										>
										<CreateForm wrappedComponentRef={(formData)=>{
											this.createForm = formData
										}}/>
									</Modal>

									<Modal
											title="设置权限"
											visible={this.state.isShowSetPrower}
											okText="确认"
											onOk={this.handleSetPowerSubmit}
											onCancel={()=>{
												this.setState({
													isShowSetPrower:false
												})
											}}
										>
										<SetPowerForm
											prowerInfo={this.state.prowerInfo}
											menuInfo = {this.state.menuInfo}
											patchMenuInfo = {(checkedKeys)=>{
												this.setState({
													menuInfo:checkedKeys
												})
											}}
											wrappedComponentRef={(formData)=>{
												this.setPowerForm = formData
											}}
										/>
									</Modal>

	            </Wrap>
	        );
	    }
	}
	// 创建角色
	class CreateForm extends React.Component{
	    render(){
	        const formItemLayout = {
	            labelCol:{
	                span:5
	            },
	            wrapperCol:{
	                span:19
	            }
	        }
	        const { getFieldDecorator }  =this.props.form;
	        return (
	            <Form layout="horizontal">
	                <FormItem label="角色名称" {...formItemLayout}>
	                    {
	                        getFieldDecorator('role_name',{
	                            initialValue:'1'
	                        })(
	                            <Select style={{ width: 100 }}>
	                                <Option value="">管理人员</Option>
	                                <Option value="1">客服专员</Option>
	                                <Option value="2">人力专员</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="营运模式" {...formItemLayout}>
	                    {
	                        getFieldDecorator('op_mode', {
	                            initialValue: '1'
	                        })(
	                            <Select style={{ width: 100 }}>
	                                <Option value="1">自营</Option>
	                                <Option value="2">加盟</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	                <FormItem label="用车模式" {...formItemLayout}>
	                    {
	                        getFieldDecorator('use_mode', {
	                            initialValue: '1'
	                        })(
	                            <Select style={{ width: 100 }}>
	                                <Option value="1">指定停车点</Option>
	                                <Option value="2">禁停区</Option>
	                            </Select>
	                        )
	                    }
	                </FormItem>
	            </Form>
	        );
	    }
	}
	CreateForm = Form.create({})(CreateForm);

	// 设置权限、
	class SetPowerForm extends React.Component{
// 渲染Tree 列表
		renderTreeNodes=(data)=>{
			return data.map((item)=>{
				if(item.children){
					return (
							<TreeNode title={item.title} key={item.key} dataRef={item}>
						 					{this.renderTreeNodes(item.children)}
					 		</TreeNode>
					)
				}
				return <TreeNode {...item} />;
			})
		}


		state = {
			expandedKeys: ['0-0-0', '0-0-1'],
			autoExpandParent: true,
			checkedKeys: ['0-0-0'],
			selectedKeys: [],
		};

		onExpand = expandedKeys => {
			console.log('onExpand', expandedKeys);
			// if not set autoExpandParent to false, if children expanded, parent can not collapse.
			// or, you can remove all expanded children keys.
			this.setState({
				expandedKeys,
				autoExpandParent: false,
			});
		};

		onCheck = checkedKeys => {
			this.props.patchMenuInfo(checkedKeys)
		};

		onSelect = (selectedKeys, info) => {
			console.log('onSelect', info);
			this.setState({ selectedKeys });
		};

			render(){
					const formItemLayout = {
							labelCol:{
									span:5
							},
							wrapperCol:{
									span:19
							}
					}

					let prower_Info = this.props.prowerInfo;
					const { getFieldDecorator }  = this.props.form;

					return (
							<Form layout="horizontal">
									<FormItem label="角色名称" {...formItemLayout}>
											<Input style={{ width: 160 }} disabled placeholder={prower_Info.role_name}/>
									</FormItem>
									<FormItem label="状态:" {...formItemLayout}>
											{
													getFieldDecorator('action',{
														initialValue:1
													})(
														<Select style={{ width: 100 }}>
																<Option value={1}>启用</Option>
																<Option value={2}>停用</Option>
														</Select>
													)
											}
									</FormItem>
									<Tree
										checkable
										defaultExpandAll
										onExpand={this.onExpand}
										expandedKeys={this.state.expandedKeys}
										autoExpandParent={this.state.autoExpandParent}
										onCheck={this.onCheck}
										checkedKeys={prower_Info.menus}
										onSelect={this.onSelect}
										selectedKeys={this.state.selectedKeys}
									>
										{this.renderTreeNodes(data)}
									</Tree>
							</Form>

					);
			}
	}
	SetPowerForm = Form.create({})(SetPowerForm);
