	import React,{ Component } from 'react';
	import axios from '../../axios';
	import startImg from '../../resource/assets/start_point.png';
	import endImg from '../../resource/assets/end_point.png';
	import BaseForm from '../../common/BaseForm';
	import {
		Wrap
	} from './style';

	import { Row,Col,Card } from 'antd';

	export default class Orederdetail extends Component{
		state={
			list:[]
		}
		componentWillMount(){
			this.requestlist();

		}

		requestlist= ()=>{
			axios.ajax({
				url:"/order/detail",
				data:{
					params:{

					}
				}
			}).then((res)=>{
				let result = res.data.result;
				this.setState({
					list:result
				})
				console.log(result);
				this.renderMap();
				this.drawServiceArea(result.area);
		  	this.rawBikeRoute(result.position_list);


			})
		}

// 创建地图 对象
		renderMap= ()=>{
			this.map = new window.BMap.Map('orderDetailMap');
			this.map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
			this.addMapControl();

		}

// 添加地图控件
		addMapControl = ()=>{
			let map = this.map;
			map.addControl(new window.BMap.ScaleControl({ anchor:window.BMAP_ANCHOR_TOP_RIGHT}));
			map.addControl(new  window.BMap.NavigationControl({ anchor:window.BMAP_ANCHOR_TOP_RIGHT }));

		}

// 绘制自己坐标点   点线面
		rawBikeRoute = (positionList)=>{
			let map = this.map;
			let startPoint = '';
			let endPoint = '';
			if(positionList.length > 0){
				let first = positionList[0];
				let last = positionList[positionList.length-1];

				// start
					let startPoint = new window.BMap.Point(first.lon,first.lat);
					let startIcon = new window.BMap.Icon(startImg,new window.BMap.Size(36,42),{
							imageSize:new window.BMap.Size(36,42),
							anchor:new window.BMap.Size(36,42)
					})
					let startMarker = new window.BMap.Marker(startPoint,{ icon:startIcon });


				// end
					let endPoint = new window.BMap.Point(last.lon,last.lat);
					let endIcon = new window.BMap.Icon(endImg,new window.BMap.Size(36,42),{
							imageSize:new window.BMap.Size(36,42),
							anchor:new window.BMap.Size(36,42)
					})
					let endMarker = new window.BMap.Marker(endPoint,{ icon:endIcon });


				// 坐标点放入数据,根据点连线
				let trackPoint = [];
				for(let i = 0;i<positionList.length;i++){
					let point = positionList[i];
					trackPoint.push(new window.BMap.Point(point.lon,point.lat));
				}

				// 创建连接线的样式
				var polyline = new window.BMap.Polyline(trackPoint, {
					strokeColor:"purple", strokeWeight:2, strokeOpacity:0.8
				});



				this.map.addOverlay(startMarker);
				this.map.addOverlay(endMarker);
				this.map.addOverlay(polyline);

				// 打开初始化地图   显示的中心点
				this.map.centerAndZoom(startPoint,11);
			}
		}

// 绘制行驶区域
		drawServiceArea = (area_list)=>{
			let map = this.map;

			let areaLists = [];
			for(let i = 0;i<area_list.length;i++){
				let point = area_list[i];
				areaLists.push(new window.BMap.Point(point.lon,point.lat));
			}

			// 创建多边形
			let polygon = new window.BMap.Polygon(areaLists, {
				strokeColor:"blue", strokeWeight:2, strokeOpacity:0.6
			});

			this.map.addOverlay(polygon);

		}




		render(){
			const {order_sn,start_location,end_location,mobile,bike_sn,user_name } = this.state.list;
			return(
				<Wrap>
					<Row>
						<Col span={24}>
							<Card>
									<div id="orderDetailMap" className="mapBox"></div>
							</Card>
							<Card>
									<div className="detail-item">
										<div className="item-title">
												基础信息
										</div>
										<div className="item-content">
												<ul>
													<li><span>用车模式</span>服务区</li>
													<li><span>订单编号</span>{order_sn}</li>
													<li><span>车辆编号</span>{bike_sn}</li>
													<li><span>用户姓名</span>{user_name}</li>
													<li><span>手机号码</span>{mobile}</li>
												</ul>
										</div>
									</div>
							</Card>

							<Card>
									<div className="detail-item">
										<div className="item-title">
												行程轨迹
										</div>
										<div className="item-content">
												<ul>
													<li><span>行程起点</span>{start_location}</li>
													<li><span>行程终点</span>{end_location}</li>
													<li><span>行驶里程</span>晋H 888688</li>
												</ul>
										</div>
									</div>
							</Card>

						</Col>
					</Row>
					<Row>

					</Row>
				</Wrap>
			)

		}

	};
