import React,{Component } from 'react';

import HomeHeader from '../../components/homeheader';
import Tab from '../../components/tab';
import HomeFooter from '../../components/homefooter';
import HomeMain from '../../components/homemain';
import UI from '../../components/ui';
	import SpinDemo from '../../components/ui/spin';
	import NotificationDemo from '../../components/ui/notification';
	import Message from '../../components/ui/message';
	import TabDemo from '../../components/ui/tab';
	import Banner from '../../components/ui/banner';
import From from '../../components/from';
	import LoginDemo from '../../components/from/login';
	import ZhuceDemo from '../../components/from/zhuce';
import Table from '../../components/table';
	import Baseictable from '../../components/table/baseicTable';
import City from '../../components/city';
import Order from '../../components/order';
import Staff from '../../components/staff';
import BikeMap from '../../components/bikemap';
// import Echarts  from '../../components/echarts';
	import Bar  from '../../components/echarts/bar/index.js';
	import Pie  from '../../components/echarts/pie/index.js';
	import Lines  from '../../components/echarts/kxian/index.js';
import Editor from '../../components/editor';
import Power from '../../components/power';

import { Row,Col } from 'antd';
import Button from 'antd/lib/button';
import { Router , Route, Link } from 'react-router-dom';

import {
	HomeWrap
} from './style';



class Home extends Component{
	render(){
		return (
			<HomeWrap>
				<Row>
				  <Col span={3} xs={6} md={4} className='leftTab' >
					<Tab/>
				  </Col>
				  <Col span={21}  xs={18} md={20} className='rightHM'>
					<HomeHeader/>

							<Route path='/home' exact  component={HomeMain}></Route>
							<Route path='/ui' exact  component={UI}></Route>
								<Route path='/ui/spin' exact  component={SpinDemo}></Route>
								<Route path='/ui/tongzhi' exact  component={NotificationDemo}></Route>
								<Route path='/ui/message' exact  component={Message}></Route>
								<Route path='/ui/tab' exact  component={TabDemo}></Route>
								<Route path='/ui/banner' exact  component={Banner}></Route>
							<Route path='/from' exact  component={From}></Route>
								<Route path='/from/login' exact  component={LoginDemo}></Route>
								<Route path='/from/zhuce' exact  component={ZhuceDemo}></Route>
							<Route path='/table' exact  component={Table}></Route>
								<Route path='/table/baseictable' exact  component={Baseictable}></Route>
							<Route path='/city' exact  component={City}></Route>
							<Route path='/order' exact  component={Order}></Route>
							<Route path='/staff' exact  component={Staff}></Route>
							<Route path='/bikemap' exact  component={BikeMap}></Route>
								<Route path='/echarts/bar' exact  component={Bar}></Route>
								<Route path='/echarts/pie' exact  component={Pie}></Route>
								<Route path='/echarts/kxian' exact  component={Lines}></Route>
								<Route path='/editor' exact  component={Editor}/>
								<Route path='/power' exact  component={Power}/>


					<HomeFooter/>
				  </Col>
				</Row>
			</HomeWrap>
		)
	}
}


export default Home;
