import React,{Component} from 'react';

import { 
	Card,Button,message
 } from 'antd';
 
import {
	Wrap		
} from './style';


class MessageDemo extends Component{
	constructor(props) {
		super(props);
	}
	 message = (type) => {
	  message[type]('恭喜你获得公司YI等奖');
	};

	render(){
		return(
			<Wrap>
					<Card title="Messgae 全局提示">
						  <Button type="primary" onClick={()=>this.message("success")}> 点击领取</Button>
						  <Button type="primary" onClick={()=>this.message("info")}> 点击领取</Button>
						  <Button type="primary" onClick={()=>this.message("warning")}> 点击领取</Button>
						  <Button type="primary" onClick={()=>this.message("error")}> 点击领取</Button>
						  <Button type="primary" onClick={()=>this.message("loading")}> 点击领取</Button>
					</Card >
				
			</Wrap>
		)
	}
}




   


