import React from 'react';
import ImageCode from './imgcode';

export default class ImgYan extends React.Component {
    state = {
        url: "https://image-static.segmentfault.com/370/831/3708310725-5c74f6055079f_articlex"
    }

		onReload = () => {
        this.setState({ url: "https://image-static.segmentfault.com/370/831/3708310725-5c74f6055079f_articlex" })
    }

    render() {
        return (
            <div>
                <ImageCode
                    imageUrl={this.state.url}
										onReload={this.onReload}
                    onMatch={() => {
                        console.log("code is match")
                    }}
                />
            </div>
        )
    }
}
