import React,{Component} from 'react';

import {
	Wrap		
} from './style';

import { Card, Form, Icon, Input, Button , message ,Checkbox} from 'antd';

class Login extends Component{
	
	componentDidMount() {
	  // To disabled submit button at the beginning.
	  this.props.form.validateFields();
	}
	
	 handleSubmit = e => {
	  e.preventDefault();
	  this.props.form.validateFields((err, values) => {
	    if (!err) {
	      console.log('数据值：', values);
		  message.success('页面下的所有表单都没问题');
	    }else{
			message.error('请刷新重试！');
		}
	  });
	};
	
	
	render(){
		const { getFieldDecorator, getFieldsError, getFieldError, isFieldTouched } = this.props.form;
		const usernameError = isFieldTouched('username') && getFieldError('username');
		const passwordError = isFieldTouched('password') && getFieldError('password');
		
		return(

			<Wrap>
				<Card title="行内登陆框">
					<Form layout="inline" onSubmit={this.handleSubmit}>
					  <Form.Item validateStatus={usernameError ? 'error' : ''} help={usernameError || ''}>
					    {getFieldDecorator('username', {
					      rules: [
							  { max:10,min:6,message:'最大值10位，最小值6位'},
							  { required: true, message: 'Please input your username!' }],
					    })(
					      <Input
					        prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
					        placeholder="Username"
					      />,
					    )}
					  </Form.Item>
					  
					  <Form.Item validateStatus={passwordError ? 'error' : ''} help={passwordError || ''}>
					    {getFieldDecorator('password', {
					      rules: [
							  { max:10,min:6,message:'最大值10位，最小值6位'},
							  { required: true, message: 'Please input your Password!' },
							  ],
					    })(
					      <Input
					        prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
					        type="password"
					        placeholder="Password"
					      />,
					    )}
					  </Form.Item>
					  <Form.Item>
					    <Button type="primary" htmlType="submit" disabled={hasErrors(getFieldsError())}>
					      Log in
					    </Button>
					  </Form.Item>
					</Form>
				</Card>
				
				
				
				<Card title="登陆框">
					 <Form onSubmit={this.handleSubmit} className="login-form">
					  <Form.Item>
					    {getFieldDecorator('loginname', {
					      rules: [
							  { max:10,min:6,message:'最大值10位，最小值6位'},
							  { required: true, message: 'Please input your username!' }],
					    })(
					      <Input
					        prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
					        placeholder="Username"
					      />,
					    )}
					  </Form.Item>
					  <Form.Item>
					    {getFieldDecorator('loginpwd', {
					      rules: [
							  { max:10,min:6,message:'最大值10位，最小值6位'},
							  { required: true, message: 'Please input your Password!' }],
					    })(
					      <Input
					        prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
					        type="password"
					        placeholder="Password"
					      />,
					    )}
					  </Form.Item>
					  <Form.Item>
					    {getFieldDecorator('remember', {
					      valuePropName: 'checked',
					      initialValue: true,
					    })(<Checkbox>记住密码</Checkbox>)}
					    <a className="login-form-forgot" href="">
					      忘记密码
					    </a>
					    <Button type="primary" htmlType="submit" className="login-form-button" disabled={hasErrors(getFieldsError())}>
					      登入
					    </Button>
					  </Form.Item>
					</Form>
				</Card>
			</Wrap>
		)
	}
}

export default Form.create({ name: 'horizontal_login' })(Login);




function hasErrors(fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field]);
}




