	import React from 'react';
	import axios from '../../axios';
	import {Wrap} from './style';
	import ETable from '../../common/ETable';
	import Utils from '../../utils';
	import {
		Card,Table,message,Button,Form, Select, Radio,DatePicker,Modal
	} from 'antd';
	import BaseForm from '../../common/BaseForm'
	import { Link } from 'react-router-dom';
// 在导入的属性内部  还有嵌套属性
	const FormItem = Form.Item;
	const Option = Select.Option;

		export default class Order extends React.Component{
			state={
				orderInfo:{},
        orderConfirmVisble:false,
				list:[],
				pagination: {},
    		loading: false,
				selectedRowKeys:[],
				selectedRows:null,

			}

			params = {
	        page: 1
	    }

	    formList = [
	        {
	            type:'SELECT',
	            label:'城市',
	            field:'city',
	            placeholder:'全部',
	            initialValue:'1',
	            width:80,
	            list: [{ id: '0', name: '全部' }, { id: '1', name: '北京' }, { id: '2', name: '天津' }, { id: '3', name: '上海' }]
	        },
	        {
	            type: '时间查询',
							width:150,
							placeholder:'Select month',
	        },
	        {
	            type: 'SELECT',
	            label: '订单状态',
	            field:'order_status',
	            placeholder: '全部',
	            initialValue: '1',
	            width: 80,
	            list: [{ id: '0', name: '全部' }, { id: '1', name: '进行中' }, { id: '2', name: '结束行程' }]
	        }
	    ]

			componentWillMount(){
				this.requestList();
			}

			handleFilter = (params)=>{
	        this.params = params;
	        this.requestList();
	    }


			handleTableChange = (pagination, filters, sorter) => {
				 const pager = { ...this.state.pagination };
				 pager.current = pagination.current;
				 this.setState({
					 pagination: pager,
				 });
				 this.requestList({
					 results: pagination.pageSize,
					 page: pagination.current,
					 sortField: sorter.field,
					 sortOrder: sorter.order,
					 ...filters,
				 });
			 };

	    // 默认请求我们的接口数据
	    requestList = ()=>{
				this.setState({ loading: true });
				let _this = this;
				axios.requestList(this,'/adminapi/order/list',this.params,10,true)
	    }

			onRowClick = (record, index) => {
					let selectKey = [index];
					this.setState({
							selectedRowKeys: selectKey,
							selectedItem: record
					})
			}

			onSelectChange=(record, index)=>{
				let selectKey = [index];
				this.setState({
					selectedRowKeys: selectKey,
					selectedItem: record
				})
			}

		 // 打开订单详情
		 openOrderdetail= ()=>{
			  let item = this.state.selectedItem;
				let key = this.state.selectedRowKeys;
				console.log(key);
				if(!item){
					Modal.info({
						title:'提示信息',
						content:'亲,请选择一条订单,进行操作~'
					})
					return;
				}
						window.open(`/#/Common/order/detail/${key.id}`,'_blank');

			}


			render(){
				const columns = [
			  {
			    title: '订单编号',
			    dataIndex: 'order_sn',
			    key: 'order_sn',
			  },
			  {
			    title: '车辆编号',
			    dataIndex: 'bike_sn',
			    key: 'bike_sn',
			  },
				{
				 title: '用户名',
				 dataIndex: 'user_name',
				 key: 'user_name',
			 },
			 {
				 title: '手机号码',
				 dataIndex: 'mobile',
				 key: 'mobile',
			 },
			 {
				 title: '里程',
				 dataIndex: 'distance',
				 key: 'distance',
			 },
			 {
				 title: '行程时长',
				 dataIndex: 'total_time',
				 key: 'total_time',
			 },
			 {
					title: '状态',
					dataIndex: 'status',
					key: 'status',
					render(action){
						return action == 1 ? '进行中' :'等待中'
					}
				},
				{
					title: '开始时间',
					dataIndex: 'start_time',
					key: 'start_time',
				},
				{
					title: '结束时间',
					dataIndex: 'end_time',
					key: 'end_time',
				},
				{
					title: '订单金额',
					dataIndex: 'total_fee',
					key: 'total_fee',
				},
				{
					title: '实付金额',
					dataIndex: 'user_pay',
					key: 'user_pay',
				}

			];

				const formItemLayout = {
						labelCol:{span:5},
						wrapperCol:{span:19}
				}
				const selectedRowKeys = this.state.selectedRowKeys;
				const rowSelection = {
	          type: 'radio',
	          selectedRowKeys
	      };
				return(
					<Wrap>
						<Card>
							<BaseForm formList={this.formList} filterSubmit={this.handleFilter}/>
						</Card>

						<Card style={{marginTop:10}}>
							<Button type="primary" onClick={this.openOrderdetail}>订单详情</Button>
							<Button type="primary">结束订单</Button>
						</Card>

						<div  className="content-wrap">
						<ETable
								updateSelectedItem ={Utils.updateSelectedItem.bind(this)}
								selectedRows = {this.state.selectedRows}
								selectedRowKeys = {this.state.selectedRowKeys}
								bordered
								columns={columns}
								dataSource={this.state.list}
								pagination={this.state.pagination}
								rowSelection={rowSelection}
						/>
						</div>

					</Wrap>
				)
			}
		}

