import React,{ Component } from 'react';
import axios from '../../axios';
import Utils from '../../utils/index.js';
import {connect} from 'react-redux';

import {
	HeaderWrap
} from './style';

import { Row,Col } from 'antd';

class Header extends Component{
	state = {
		sysTime:'',
		weather:' 多云转晴 °C / ',
		dayPictureUrl:'',
		city:'忻州市'
	}
	componentWillMount(){
		this.setState({
			username:'Haisen‘s',
		})

		setInterval(()=>{
			let sysTime = Utils.formDate(new Date().getTime());
			this.setState({
				sysTime,

			})
		},1000)

		this.getWeatherAPIData();
		this.getIP();


	}

	getIP(){
		axios.IPXY({
			url:'http://api.map.baidu.com/location/ip?&ak=DXUId5ZkyT96FM36uI25pGqEoFqoiuQT&coor=bd09ll'
		}).then((res)=>{
			if(res.status = '0'){
			let IPcity  = res.content.address_detail.city;
			this.setState({
				city:IPcity
			})
			}
		})
	}


		getWeatherAPIData(){
				const city = this.state.city;
				axios.jsonp({
					url:'http://api.map.baidu.com/telematics/v3/weather?location='+encodeURIComponent(city)+'&output=json&ak=3p49MVra6urFRGOT9s8UBWr2'
				}).then((res)=>{
					if(res.status == 'success'){
						let data = res.results[0].weather_data[1];
						this.setState({
							dayPictureUrl:data.dayPictureUrl,
							weather:data.weather
						})
					}
				})
			}




	render(){
		const { username,sysTime,weather, dayPictureUrl,city} = this.state;
		return(
			<HeaderWrap>
				<Row className="HeaderT1">
					<Col span={24}>
						<span>欢迎,<b>{username}</b></span>
						<a href="#">退出</a>
					</Col>
				</Row>
				<Row className="HeaderT2">
					<Col span={4}>
						<h1>首页</h1>
					</Col>
					<Col span={20}>
						<span className="tianqi">
							<img src={dayPictureUrl} style={{height:25,marginRight:5}}/>
							天气:{weather}
						</span>
						<span className="shijian">时间: <b>{sysTime}</b></span>
						<span>当前所在地:<b>{city}</b></span>
					</Col>
				</Row>
			</HeaderWrap>
		)

	}

};



export default connect()(Header);
