import React,{Component} from 'react';
import {Card, Tabs, Icon ,message} from 'antd';
import {
	Wrap
} from './style';
class TabDemo extends Component {
	callBack = (key)=>{
		message.info('Hi,您选择了也签' + key);
	}
	render(){
		const { TabPane } = Tabs;
		return(
			<Wrap>
				<Card title="Tab">
					<Tabs defaultActiveKey="3" onChange={this.callBack}>
					  <TabPane  tab={
					      <span>
					        <Icon type="apple" />
					        Tab 1
					      </span>
						}
					    key="1"
					  >
					  
					    Tab 1
						
					  </TabPane>
					  
					  <TabPane
					    tab={
					      <span>
					        <Icon type="android" />
					        Tab 2
					      </span>
					    }
					    key="2"
					  >
					    Tab 2
					  </TabPane>
					  
					  <TabPane
					    tab={
					      <span>
					        <Icon type="android" />
					        Tab 3
					      </span>
					    }
					    key="3"
					  >
					    自己添加一个小Tab  简简单单 省了好多事儿
					  </TabPane>
					  
					</Tabs>
				</Card>
			</Wrap>
		)
	}
	
}

export default TabDemo;





