import React,{Component} from 'react';
	import axios from '../../axios';
import {
	Card,Table
}from 'antd';

import {
	Wrap
}from './style';

class BaseicTable extends Component {

	componentWillMount(){
		axios.ajax({
			url:'/adminapi/dt/table',
			data:{
				isShowLoading:true,
				params:{
					page:1
				}
			}

		}).then((res)=>{
				const result = res.data.result;
				console.log(res);
				this.setState({
					dataapiSource:result
				})
			}
		)


	}

	state={
		dataapiSource :[],

	}


	render(){
		const dataSource = [
			  {
				key: '1',
				username: '胡彦斌',
				age: 32,
				address: '西湖区湖底公园1号',
				live:'Wo 住在《澎湖湾》'
			  },
			  {
				key: '2',
				username: '胡彦祖',
				age: 42,
				address: '西湖区湖底公园1号',
				live:'I live in 《童话镇》'
			  },
			  {
				key: '3',
				username: '暗杠',
				age: 23,
				address: '西湖区湖底公园1号',
				live:'I live in 《童话镇Plus》'
			  },
			];

			const columns = [
			  {
				title: '姓名',
				dataIndex: 'username',
				key: 'username'

			  },
			  {
				title: '年龄',
				dataIndex: 'age',
				key: 'age',
			  },
			  {
				title: '住址',
				dataIndex: 'address',
				key: 'address',
			  },
			  {
				  title:'你家是哪里的？',
				  dataIndex: 'live',
				  key: 'live',
			  }
			];

		return(
			<Wrap>
				<Card title="基础表格">
					<Table bordered dataSource={dataSource} columns={columns} />
				</Card>

				<Card title="动态数据渲染表格">
					<Table bordered dataSource={this.state.dataapiSource} columns={columns} />
				</Card>
			</Wrap>
		)
	}

}

export default BaseicTable;
