  import React from 'react';
  import axios from '../../axios';
  import Utils from '../../utils';
  import BaseForm from '../../common/BaseForm';
  import Etable from '../../common/ETable';
  import {Card,Form,} from 'antd';
  import {Wrap} from './style';

  import startImg from '../../resource/assets/start_point.png';
  import endImg from '../../resource/assets/end_point.png';
  import bikeImg from '../../resource/assets/bike.jpg';

  export default class BikeMap extends React.Component{
    state={

      list:[],
      pagination: {},
      loading: false,
      isShowAddUse:false,
      title:"",
      type:"",
      useInfo:""

    }

    formList = [
      {
        type:"城市",
        label:'城市',
        width:80,
        initialValue:'北京',
        list:[{id:0,name:'北京'},{id:1,name:'上海'},{id:2,name:'广州'}]
      },
      {
        type:"时间查询",
        label:'时间查询',
        placeholder:'Select mount',
        width:160
      },
      {
        type:'SELECT',
        label:'订单查询',
        field:'order_status',
        placeholder:'全部',
        initialValue:'全部',
        width:80,
        list:[{id:0,name:'全部'},{id:1,name:'进行中'},{id:2,name:'已完成'}]
      }
    ]

    componentWillMount(){
      this.requestList()
    }

    handleFilter = (params)=>{
        this.params = params;
        this.requestList();
    }


    requestList= ()=>{
      axios.ajax({
        url:'/adminapi/map/bike_list',
        data:{
          params:{

          }
        }
      }).then((res)=>{
        let result = res.data.result;
        this.setState({
          list:result
        })

        let bike_list = result.bike_list;
        let route_list = result.route_list;
        let service_list = result.service_list;
        // 载入初始化
        this.renderMap();
        // 等待自行车的 位置坐标
        this.bikeGps(bike_list);
        // 可以监听服务区域  超出就不归我管了
        this.drawServiceList(service_list);
        // 自行车  服务,起点到终点的
        this.gpsGps(route_list);

      })
    }

    filterSubmit = (filterParams)=>{
      this.params = filterParams;
    }


// ------------------------------------------------

// 创建地图 对象    找到显示的位置 id盒子
    renderMap= ()=>{
      this.map = new window.BMap.Map('BikeDetailMap');
      this.map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
      this.addMapControl();  //添加控件

    }
//  GPS 定位 开始结束位置
    gpsGps = (list)=>{

// 起点
        let gpsStart = list[0].split(',');
        let startPoint = new window.BMap.Point(gpsStart[0],gpsStart[1]);
        let startIcon = new window.BMap.Icon(startImg,new window.BMap.Size(36,42),{
            imageSize:new window.BMap.Size(36,42),
            anchor:new window.BMap.Size(18,42)
        })
        let startMarker = new window.BMap.Marker(startPoint,{ icon:startIcon });

// 结束
        let gpsLast = list[list.length-1].split(',');
        let endPoint = new window.BMap.Point(gpsLast[0],gpsLast[1]);
        let endIcon = new window.BMap.Icon(endImg,new window.BMap.Size(36,42),{
            imageSize:new window.BMap.Size(36,42),
            anchor:new window.BMap.Size(18,42)
        })
        let endMarker = new window.BMap.Marker(endPoint,{ icon:endIcon });
// 遍历所有的 路线
        let routeList = [];
        list.forEach((item)=>{
            let p = item.split(',');
            routeList.push(new window.BMap.Point(p[0],p[1]));
        })

        let poliLine = new window.BMap.Polyline(routeList, {
					strokeColor:"red", strokeWeight:2, strokeOpacity:0.8
				});




// 添加   动作
        this.map.addOverlay(startMarker);
        this.map.addOverlay(endMarker);
        this.map.addOverlay(poliLine);
        // 打开初始化地图   显示的中心点
        this.map.centerAndZoom(endPoint,11);

    }

// 添加地图控件  默认操作  写入这可就可以添加控件操作了
    addMapControl = ()=>{
      let map = this.map;
      map.addControl(new window.BMap.ScaleControl({ anchor:window.BMAP_ANCHOR_TOP_RIGHT}));
      map.addControl(new  window.BMap.NavigationControl({ anchor:window.BMAP_ANCHOR_TOP_RIGHT }));

    }

// 绘制行驶区域
    drawServiceList = (list)=>{
      let serverList = [];
      list.forEach((item)=>{
          serverList.push(new window.BMap.Point(item.lon,item.lat));
      })

      // 创建多边形
      let polygon = new window.BMap.Polygon(serverList, {
        strokeColor:"blue", strokeWeight:2, strokeOpacity:0.6
      });

      this.map.addOverlay(polygon);

    }

// 等待中的 自行车
    bikeGps=(list)=>{
      let bikeList = [];
      list.forEach((item,index)=>{
         console.log(item);
         let bike = item.split(',');
         let bikeItem =  new window.BMap.Point(bike[0],bike[1]);
         let bikeIcon = new window.BMap.Icon(bikeImg,new window.BMap.Size(36,42),{
             imageSize:new window.BMap.Size(36,42),
             anchor:new window.BMap.Size(18,42)
         })
         let bikeMarker = new window.BMap.Marker(bikeItem,{ icon:bikeIcon });
         this.map.addOverlay(bikeMarker);
      })
    }








    render(){
      const columns=[
        {
          title:'id',
          dataIndex:'id'
        },
        {
          title:'员工名称',
          dataIndex:'username'
        },
        {
          title:'性别',
          dataIndex:'sex',
          render(sex){
            return sex == '1' ? '男':'女'
          }

        },
        {
          title:'状态',
          dataIndex:'state'
        },
        {
          title:'兴趣',
          dataIndex:'interest',
          render(more){
            if(more == 0){
              return  '踢足球'
            }else if(more == 1){
              return   '游泳'
            }else if(more == 2){
              return   '旅行'
            }else if(more == 3){
              return   '爬山'
            }else if(more == 4){
              return   '摄影'
            }else if(more == 5){
              return   '做饭'
            }else if(more == 6){
              return   '养宠物'
            }
          }
        },
        {
          title:'到达否',
          dataIndex:'isMarried',
          render(isMarried){
            return isMarried == '1' ? '已到达':'未到达'
          }
        },
        {
          title:'birthday',
          dataIndex:'birthday'
        },
        {
          title:'address',
          dataIndex:'address'
        },
        {
          title:'入职时间',
          dataIndex:'time'
        }
      ]
      const selectedRowKeys = this.state.selectedRowKeys;
      const rowSelection = {
          type: 'radio',
          selectedRowKeys
      };
      return(
        <Wrap>
          <Card title="筛选查询定位">
              <BaseForm formList={this.formList} filterSubmit={this.filterSubmit}/>
          </Card>
          <Card>
            <div>共有100辆车</div>
            <div className="content-wrap" style={{height:500}}>
                <div id="BikeDetailMap" style={{height:500}}></div>
            </div>
          </Card>
        </Wrap>
      )
    }
  }
