	import React,{Component} from 'react';
	import CommonHeader from '../../components/commonheader';

	import { Row,Col } from 'antd';
	import {
		Wrap
	} from './style';


	export default class Common extends Component{
		render(){
			return(

				<Wrap>
					<Row>
						<CommonHeader/>
					</Row>
					<Row>
						<Col span={24}>
							{this.props.children}
						</Col>
					</Row>

				</Wrap>
			)
		}
	}






