  import React from 'react';
  import {
		Card,Table,message,Button,Form, Select, Radio,DatePicker,Modal,Input
	} from 'antd';
  import BaseForm from '../../common/BaseForm';
  import ETable from '../../common/ETable';
  import Utils from '../../utils';
  import {Wrap} from './style';
  import axios from '../../axios';

  import moment from 'moment';
  const FormItem = Form.Item;
  const Option = Select.Option;
  const TextArea = Input.TextArea;

  export default class Staff extends React.Component{
    state={

      list:[],
      pagination: {},
      loading: false,
      isShowAddUse:false,
      title:"",
      type:"",
      useInfo:""

    }

    params = {

    }

    formList =[
      {
        type:"INPUT",
        placeholder:'用户名',
        width:80,
        field:'username'
      },
      {
        type:"INPUT",
        placeholder:'密码',
        width:80,
        field:'password'
      },
      {
        type:"DATE",
        width:150,
        placeholder:'Select month',
      }
    ]

    componentWillMount(){
      this.requestList()
    }

    handleFilter = (params)=>{
        this.params = params;
        this.requestList();
    }

    requestList= ()=>{
    	this.setState({ loading: true });
      let _this = this;
      axios.requestList(this,'/adminapi/user/list',this.params,5,true)
    }

    handleTableChange = (pagination, filters, sorter) => {
       const pager = { ...this.state.pagination };
       pager.current = pagination.current;
       this.setState({
         pagination: pager,
       });
       this.requestList({
         results: pagination.pageSize,
         page: pagination.current,
         sortField: sorter.field,
         sortOrder: sorter.order,
         ...filters,
       });
     };

     onRowClick = (record, index) => {
         let selectKey = [index];
         this.setState({
             selectedRowKeys: selectKey,
             selectedItem: record
         })
     }

     // Button  按钮操作
     handleStaff = (methods)=>{
       let item = this.state.selectedItem;
       switch (methods){
         case 'add':
           this.setState({
             type:methods,
             isShowAddUse:true,
             title:"创建员工"
           })
           break;
         case 'edit':
           if(!item){
             Modal.info({
               title:'编辑提示',
               content:'请选择一条信息修改'
             })
           }else{
             this.setState({
               type:methods,
               isShowAddUse:true,
               title:"编辑员工",
               useInfo:item
             })
           }

           break;
         case 'detail':
         if(!item){
           Modal.info({
               title:'编辑提示',
               content:'请选择一条信息查看'
             })
           }else{
             this.setState({
               type:methods,
               isShowAddUse:true,
               title:"员工信息",
               useInfo:item
             })
           }
           break;
         case 'delete':
         let _this = this;
           if(!item){
             Modal.info({
               title:'警告',
               content:'请选择一条记录进行删除'
             })
           }else{
             Modal.confirm({
                 title:'确认删除此记录',
                 onOk(){
                   axios.ajax({
                     url:'/user/delete',
                     data:{
                       params:{
                         id:item.id
                       }
                     }
                   }).then((res)=>{
                     if(res){
                       _this.setState({
                         isShowAddUse:false
                       })
                       _this.requestList();
                     }
                   })
                 }
               })
           }
           break;
         default:
           return;
       }
     }

     // 创建员工提交
     handleSubmit = ()=>{
       let data = this.userForm.props.form.getFieldsValue();
       let methods = this.state.type;
       axios.ajax({
         url:methods == "add" ?'/user/add':'/user/edit' ,
         data:{
           params:data
         }
       }).then((data)=>{
         if(data){
          this.setState({
            isShowAddUse:false
          })
         }
         this.requestList();
       })
     }

     colse = ()=>{
       this.setState({
         useInfo:null
       })
     }


    render(){
      const columns=[
        {
          title:'id',
          dataIndex:'id'
        },
        {
          title:'员工名称',
          dataIndex:'username'
        },
        {
          title:'性别',
          dataIndex:'sex',
          render(sex){
            return sex == '1' ? '男':'女'
          }

        },
        {
          title:'状态',
          dataIndex:'state'
        },
        {
          title:'兴趣',
          dataIndex:'interest',
          render(more){
            if(more == 0){
              return  '踢足球'
            }else if(more == 1){
              return   '游泳'
            }else if(more == 2){
              return   '旅行'
            }else if(more == 3){
              return   '爬山'
            }else if(more == 4){
              return   '摄影'
            }else if(more == 5){
              return   '做饭'
            }else if(more == 6){
              return   '养宠物'
            }
          }
        },
        {
          title:'到达否',
          dataIndex:'isMarried',
          render(isMarried){
            return isMarried == '1' ? '已到达':'未到达'
          }
        },
        {
          title:'birthday',
          dataIndex:'birthday'
        },
        {
          title:'address',
          dataIndex:'address'
        },
        {
          title:'入职时间',
          dataIndex:'time'
        }
      ]
      const selectedRowKeys = this.state.selectedRowKeys;
      const rowSelection = {
          type: 'radio',
          selectedRowKeys
      };
      let footer ={};
      if(this.state.type == 'detail'){
        footer={
          footer:null
        }
      }
      return(
        <Wrap>
          <Card title="员工信息查询" style={{height:150}}>
            <BaseForm formList={this.formList} filterSubmit={this.handleFilter}/>
          </Card>
          <Card>
            <Button type="primary" icon="plus" onClick={()=>this.handleStaff('add')}>添加员工</Button>
            <Button type="primary" icon="edit" onClick={()=>this.handleStaff('edit')}>编辑员工</Button>
            <Button type="primary" icon="user" onClick={()=>this.handleStaff('detail')}>员工详情</Button>
            <Button type="primary" icon="delete" onClick={()=>this.handleStaff('delete')}>删除员工</Button>
          </Card>
          <div className="content-wrap">
          <ETable
              updateSelectedItem ={Utils.updateSelectedItem.bind(this)}
              selectedRows = {this.state.selectedRows}
              selectedRowKeys = {this.state.selectedRowKeys}
              bordered
              columns={columns}
              dataSource={this.state.list}
              pagination={this.state.pagination}
              rowSelection={rowSelection}
          />
          </div>
          <Modal
              keyboard
              afterClose={this.colse}
              title={this.state.title}
              visible={this.state.isShowAddUse}

              onOk={this.handleSubmit}
              onCancel={()=>{
                this.setState({
                  isShowAddUse:false
                })
              }}
              {...footer}
            >
            <AddUse type={this.state.type} useInfo={this.state.useInfo} wrappedComponentRef={(formData)=>{
              this.userForm = formData
            }}/>
          </Modal>
        </Wrap>
      )
    }

  }






  class AddUse extends React.Component{

    getState = (state)=>{
      return{
        '1':'咸鱼一条',
        '2':'风华正茂',
        '3':'北大才子一条',
        '4':'百度Seo职员'
      }[state]
    }
    getInterest = (interest)=>{
      return{
        '1':'足球',
        '2':'篮球',
        '3':'乒乓球',
        '4':'羽毛球'
      }[interest]
    }
	    render(){

        let type = this.props.type;
        let useInfo = this.props.useInfo || {};
        const { getFieldDecorator }  =this.props.form;
	      const formItemLayout = {
	            labelCol:{
	                span:5
	            },
	            wrapperCol:{
	                span:19
	            }
	        }

	        return (
	            <Form layout="horizontal">
	                <FormItem label="姓名:" {...formItemLayout}>
	                    {
                          type == 'detail' ? useInfo.username :
	                        getFieldDecorator('usename',{
	                            initialValue:useInfo.username
	                        })(
	                            <Input style={{ width: 160 }} value="员工姓名"/>
	                        )
	                    }
	                </FormItem>
                  <FormItem label="性别:" {...formItemLayout}>
	                    {
                          type == 'detail' ? (useInfo.sex == 1 ? '男':'女') :
	                        getFieldDecorator('sex',{
                            initialValue:useInfo.sex
                          })(
                            <Radio.Group >
                              <Radio value={1}>男</Radio>
                              <Radio value={2}>女</Radio>
                            </Radio.Group >
	                        )
	                    }
	                </FormItem>
                  <FormItem label="状态:" {...formItemLayout}>
	                    {
                          type == 'detail' ? this.getState(useInfo.state) :
	                        getFieldDecorator('state',{
                          initialValue:useInfo.state
                          })(
                          <Select style={{ width: 160 }}>
                              <Option value={1}>入职一个月</Option>
                              <Option value={2}>入职3个月</Option>
                              <Option value={3}>入职半年</Option>
                              <Option value={4}>正式员工</Option>
                          </Select>
	                        )
	                    }
	                </FormItem>
                  <FormItem label="兴趣:" {...formItemLayout}>
	                    {
                          type == 'detail' ? this.getInterest(useInfo.interest) :
	                        getFieldDecorator('interest',{
                          initialValue:useInfo.interest
                          })(
                          <Select style={{ width: 160 }}>
                              <Option value={1}>足球</Option>
                              <Option value={2}>篮球</Option>
                              <Option value={3}>乒乓球</Option>
                              <Option value={4}>羽毛球</Option>
                          </Select>
	                        )
	                    }
	                </FormItem>
                  <FormItem label="到达否:" {...formItemLayout}>
	                    {
                          type == 'detail' ? (useInfo.isMarried == 0?'已到达':'未到达') :
	                        getFieldDecorator('isMarried',{
                          initialValue:useInfo.isMarried
                          })(
                            <Radio.Group >
                              <Radio value={0}>到达</Radio>
                              <Radio value={1}>未签到</Radio>
                            </Radio.Group >
	                        )
	                    }
	                </FormItem>
                 <FormItem label="生日:" {...formItemLayout}>
                    {
                        type == 'detail' ? useInfo.birthday :
                        getFieldDecorator('birthday',{
                          initialValue:moment(useInfo.birthday)
                        })(
                            <DatePicker/>
                        )
                    }
                </FormItem>
                <FormItem label="联系地址:" {...formItemLayout}>
                   {
                      type == 'detail' ? useInfo.address :
                       getFieldDecorator('address',{
                         initialValue:useInfo.address
                       })(
                           <TextArea row={3} placeholder="请输入联系地址...">

                           </TextArea>
                       )
                   }
               </FormItem>
	            </Form>
	        );
	    }
	}
	AddUse = Form.create({})(AddUse);
