import styled from 'styled-components';


export const Wrap = styled.div`
	width:100%;
	height:calc(85vh);
	background:#F5F5F5;
	color:#000;
	font-weight:bold;
	padding:20px;
	overflow:auto;
	display: flex;
	flex-direction:column;

	Button{
		margin:5px;
	}
	
	div{
		margin-bottom:5px;
	}
	
	.bannerItem{


		background:red;
		display: flex;
		justify-content:center;
		align-items:center;
		text-align:center;
		img{
			width:100%;
			height:calc(50vh);
		}
		
	}

`;